    border-color: #d6e9c6;
}  
.no-margin{
   
    margin-top: 0px; 
    margin-bottom: 0px;
    
}
.summary-padding-bottom{
    padding-bottom:2rem;
}
.grey-border-radius{
    box-shadow: 0rem 0.1rem 0.3rem grey;
    /* border:2px solid grey; */
    border-radius:1rem;
}
.no-margin{
    margin-right: 3rem !important;
    margin-left: 3rem !important;
}
.padding-summary{
    padding:2rem;
}

.white-bg{
    background-color: white;
}
.round-border{
    border-radius:50%;
    /* width:20rem; */
    height: 15rem; 

    border: 3px solid rgb(10, 105, 143);
}


@media (max-width: 991px){
    .circle-score {
        border:2px solid #29ABE1;
        border-radius: 50%;
        height: 90px;
        width: 90px;
        display: table;
        margin-left: 9rem;
        padding-right: 0px !important; 
        padding-left: 0px !important;
    }
    .circle-score h3 {
        vertical-align: middle;
        padding-top:15%;
        font-size:1.3rem;
    }
    .circle-score h4{
        font-size:2rem;
        /* width: 120px; */
        margin-top:-7px !important;
    }
    .no-margin{
        margin-right: 0rem !important;
        margin-left: 0rem !important;
    }
    .summary-title{
        font-size: 2rem;
    }
    #viewReport, #seeMore{
        color:#25D69D;
        text-align: center;
        font-size: 1.5rem;
    }
    .text-font-size h3{
        font-size:1.5rem !important;
    }
    .text-font-size h4{
        font-size:1.3rem !important;
    }
    .skills-score{
        text-align: right;
        margin-top: -2.5rem;
    }
    .skills-label{
        text-align: left;
    }

}
